@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@700&display=swap);
body{margin:0 auto;font-family:'Space Mono', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#c5e4e7}

.header{text-align:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.header__item{text-transform:uppercase;font-size:36px;margin:0;letter-spacing:6px}

.App {
  text-align: center;
  color: rgb(67, 79, 90);
  box-sizing: border-box;
}

.form{display:-webkit-flex;display:flex;-webkit-flex-direction:column-reverse;flex-direction:column-reverse;-webkit-justify-content:center;justify-content:center;grid-gap:20px;gap:20px;background-color:white;border-radius:15px;box-shadow:rgba(0,0,0,0.4) 0px 2px 4px,rgba(0,0,0,0.3) 0px 7px 13px -3px,rgba(0,0,0,0.2) 0px -3px 0px inset;padding:20px 15px;max-width:320px;margin:0 auto}.fields{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;grid-gap:15px;gap:15px}.field__label{text-align:left;text-transform:capitalize;color:#60706f;padding-bottom:5px}.field__bill{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:flex-start;align-items:flex-start}.field__bill-input{width:290px;padding:0 15px;background-color:#f0f8ff;border:none;font-family:"Space Mono",sans-serif;background-image:url(/splitter/static/media/icon-dollar.9f9be353.svg);background-repeat:no-repeat;background-position:5% 50%}.field__tip{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:10px;gap:10px;place-items:center}.field__tip-wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.field__tip-num{font-family:"Space Mono",sans-serif;width:90px;padding:0;border:none;cursor:pointer;background-color:#005252;color:#f0f8ff;height:30px;transition:all 0.2s;font-size:14px;border-radius:5px}.field__tip-num:hover:not(:last-child),.field__tip-num:focus:not(:last-child){background-color:#3dada5;color:#005252}.field__tip-num:focus:not(:last-child){-webkit-transform:scale(0.9);transform:scale(0.9);font-size:16px}.field__tip-custom{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:0}.field__people{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:flex-start;align-items:flex-start;-webkit-flex-direction:column;flex-direction:column}.field__people-input{width:290px;padding:0 15px;background-color:#f0f8ff;background-image:url(/splitter/static/media/icon-person.65bfd204.svg);background-repeat:no-repeat;background-position:5% 50%}.field__people-input::-webkit-input-placeholder{font-family:"Space Mono",sans-serif}.field__people-input:-ms-input-placeholder{font-family:"Space Mono",sans-serif}.field__people-input::placeholder{font-family:"Space Mono",sans-serif}.results{background-color:#005252;padding:20px;border-radius:10px;box-shadow:rgba(0,0,0,0.4) 0px 2px 4px,rgba(0,0,0,0.3) 0px 7px 13px -3px,rgba(0,0,0,0.2) 0px -3px 0px inset}.results__value{color:#3dada5}.results__info{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.results__header{margin:0;text-transform:capitalize;font-size:12px;color:#f0f8ff}.results__subheader{margin:0;font-size:9px;color:#32afb1;-webkit-align-self:flex-start;align-self:flex-start}.results__item{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.results__reset{background-color:#005252;width:75%;height:30px;border-radius:4px;border:none;cursor:pointer;text-transform:uppercase;letter-spacing:4px;font-family:"Space Mono",sans-serif;transition:all 0.2s;color:#32afb1}.results__reset:hover{background-color:#3dada5;box-shadow:rgba(60,64,67,0.3) 0px 1px 2px 0px,rgba(60,64,67,0.15) 0px 1px 3px 1px;border:1px solid #32afb1;color:#60706f}.results__reset:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.input{border:none;height:30px;border-radius:5px;font-family:"Space Mono",sans-serif;direction:rtl;color:#60706f}.input:focus,.input:hover{outline:none;box-shadow:0 0 0 2px #3dada5}.input:active{-webkit-transform:none;transform:none}.input::-webkit-outer-spin-button,.input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.custom{background-color:#f0f8ff;text-align:center}@media (min-width: 744px){.form{max-width:768px;-webkit-flex-direction:row;flex-direction:row}.fields{width:45%}.field__bill-input{width:300px;padding:0 15px}.field__people-input{width:300px;padding:0 15px}.field__tip{place-items:start}.results{width:45%}}

